import "./styles.css";
import logo from "./images/logo.svg";
import data from "./data.json";

export default function App() {
  const days = ["sun", "mon", "tue", "wed", "thu", "fri", "sat"];
  const today = days[new Date().getDay()];

  return (
    <div className="App">
      <div className="main">
        <div className="header">
          <div className="balance">
            <p>My balance</p>
            <h2>$921.48</h2>
          </div>
          <img src={logo} alt="logo" />
        </div>
        <div className="details">
          <h2>Spending - Last 7 days</h2>
          <div className="chart">
            {data.map((e, index) => {
              return (
                <div
                  key={index}
                  className="bar"
                  style={{ width: `${100 / data.length - 3}%` }}
                >
                  <p className="spent">{`$${e.amount}`}</p>
                  <div
                    className="barEl"
                    style={{
                      height: `${e.amount * 4}px`,
                      backgroundColor:
                        e.day === today ? "var(--cyan)" : "var(--softRed)"
                    }}
                  ></div>
                  <p>{e.day}</p>
                </div>
              );
            })}
          </div>
          <div className="total">
            <div className="thisMonth">
              <p>Total this month</p>
              <h2>$478.33</h2>
            </div>
            <div className="lastMonth">
              <h4>+2.4%</h4>
              <p>from last month</p>
            </div>
          </div>
        </div>
      </div>
      <div className="attribution">
        Challenge by{" "}
        <a
          href="https://www.frontendmentor.io?ref=challenge"
          target="_blank"
          rel="noreferrer"
        >
          Frontend Mentor
        </a>
        . Coded by <a href="https://github.com/younessoub">Youness</a>.
      </div>
    </div>
  );
}
